/* 576, 992, 1200, 1400 bp */
/* 375 design */
.liquidLanding {
  overflow: hidden;
}

.pageWrap {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.backDropImages {
  position: absolute;
  width: 15vw;
  display: flex;
  margin-left: 46px;
}
.imgPhone {
  width: 100%;
  margin-right: 30px;
}
.imgPixel {
  width: 100%;
}

.button_primary {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 188px;
  height: 54px;
  background: #28a5eb;
  border-radius: 12px;

  color: #ffffff;
  /* font-family: Inter; */
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  cursor: pointer;
}
.button_primary:hover {
  background: #1389cd;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}
.button_white {
  background: #fff;
  color: #28a5eb;
}
.button_white:hover {
  background: #fff;
}

@media (max-width: 992px) {
  .imgPixel {
    display: none;
  }
}

/* ---------- BackgroundImages ---------- */
.backCL {
  width: 100%;
  position: absolute;
  z-index: -1;
  overflow: hidden;
}
.backCL__img {
  object-fit: cover;
  object-position: -18em 0px;
  width: 136%;
}
@media (max-width: 992px) {
  .backCL {
    display: none;
  }
}
.group {
  position: absolute;
  z-index: -1;
  top: 100vh;
  left: 17%;
}

/* ---------- Header ---------- */
.header {
  display: flex;
  justify-content: space-between;
  padding-top: 22px;
}
.logo {
  height: 41px;
}

.header__right {
  display: flex;
  align-items: center;
  z-index: 10;
}
/* #9DAFBD */
.chat,
.notification,
.user {
  cursor: pointer;
}
.chat,
.notification {
  height: 20px;
  margin-right: 30px;
}
.user {
  height: 41px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 40px;
}
.user:hover {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

.chat,
.notification {
  opacity: 0.7;
  height: 40px;
  width: 40px;
}
.chat:hover,
.notification:hover {
  opacity: 1;
}
.chat path,
.notification path {
  fill: #7e97a9 !important;
  stroke-width: 0.1;
}
.chat:hover,
.notification:hover {
  fill: #627e93 !important;
}
.chat path:hover,
.notification path:hover {
  fill: #627e93 !important;
  filter: drop-shadow(3px 5px 2px rgb(0 0 0 / 0.1));
}

@media (max-width: 1200px) {
  .header {
    margin-left: 16px;
    margin-right: 16px;
  }
  .logo {
    height: 36px;
  }
  .chat,
  .notification {
    margin-right: 16px;
  }
}
@media (max-width: 576px) {
  .chat,
  .notification,
  .user {
    /* height: 50px;
    width: 50px; */
  }
}

/* ---------- MainView ---------- */
.mainView {
